"use client";
import Link from 'next/link';

export default function EmbedDemoPage() {
  return (
    <div className="flex flex-col min-h-screen bg-[var(--background)] text-[var(--foreground)] relative overflow-x-hidden">
      {/* 祥云装饰 */}
      <div className="chinese-cloud" style={{top: 24, left: 32}}>
        <svg viewBox="0 0 80 40" fill="none" xmlns="http://www.w3.org/2000/svg">
          <path d="M10 30 Q 20 10, 40 20 T 70 25" stroke="#e7b85a" strokeWidth="3" fill="none"/>
        </svg>
      </div>
      <main className="flex-grow w-full h-full relative z-10">
        <div className="fixed top-0 left-0 w-full z-20 bg-black/80 border-b border-[var(--border)] shadow-lg">
          <div className="container mx-auto flex items-center justify-between px-4 py-3">
            <div className="flex items-center gap-4">
              <Link href="/" className="text-[var(--border)] hover:text-[var(--accent)] text-xl font-bold transition-colors" style={{fontFamily: 'var(--font-chinese)'}}>← 返回首页</Link>
              <span className="text-lg md:text-2xl font-bold text-[var(--border)] drop-shadow" style={{fontFamily: 'var(--font-chinese)'}}>QAnything 问答服务嵌套演示</span>
            </div>
            <span className="text-[var(--accent)] text-sm hidden md:block" style={{fontFamily: 'var(--font-chinese)'}}>中国风嵌入式页面案例</span>
          </div>
        </div>
        <section className="max-w-3xl mx-auto mt-24 mb-6 chinese-border bg-black/70 p-6 relative z-20">
          <h2 className="text-xl font-bold mb-2 text-[var(--border)]" style={{fontFamily: 'var(--font-chinese)'}}>案例说明</h2>
          <ul className="list-disc list-inside text-[var(--accent)] space-y-1 mb-1" style={{fontFamily: 'var(--font-chinese)'}}>
            <li>本案例演示了如何在 Next.js 页面中嵌入第三方智能问答服务（QAnything）。</li>
            <li>顶部悬浮标题区采用中国风美化，iframe 区域全屏沉浸式体验。</li>
            <li>如需体验交互，请直接在下方问答区输入问题。</li>
          </ul>
        </section>
        <iframe
          src="https://ai.youdao.com/saas/qanything/#/home"
          title="QAnything 问答服务"
          width="100%"
          height="100%"
          className="fixed left-0 w-full z-10"
          style={{
            top: 112, // 标题+说明高度
            height: 'calc(100vh - 112px)',
            border: 'none',
          }}
          allowFullScreen
        />
      </main>
      {/* 古建筑底部装饰 */}
      <div className="chinese-building">
        <svg viewBox="0 0 220 90" fill="none" xmlns="http://www.w3.org/2000/svg">
          <rect x="30" y="60" width="160" height="20" fill="#bfa76a" stroke="#e7b85a" strokeWidth="3"/>
          <path d="M30 60 Q 110 10, 190 60" stroke="#e7b85a" strokeWidth="5" fill="none"/>
          <rect x="80" y="40" width="60" height="20" fill="#bfa76a" stroke="#e7b85a" strokeWidth="2"/>
          <path d="M80 40 Q 110 20, 140 40" stroke="#e7b85a" strokeWidth="3" fill="none"/>
        </svg>
      </div>
    </div>
  );
} 